import {
  Inline,
  RangeSelection,
  Decoration,
  Node,
  Text,
  Action
} from '@ali/4ever-cangjie';
import { BlockLinkCard, ConvertBlockLinkCardToTextLinkPayload, ConvertPureLinkToBlockLinkCardPayload, ConvertTextLinkToBlockLinkCardPayload, CopyOrCutBlockLinkCardPayload, ILinkCardInfo, InsertBlockLinkCardPayload, IRange, LinkDisplayType, LinkNode, SaveLinkCardPayload, ShowLinkPastePanelPayload, UnlinkBlockLinkCardPayload, UpdateBlockLinkCardDataPayload, UpdatePureLinkPayload } from '../types';

/* #region  actionTypes */
export const LINK_CARD_PURE_LINK_TO_BLOCK_LINK_CARD = Symbol('LINK_CARD_PURE_LINK_TO_BLOCK_LINK_CARD');

export const LINK_CARD_TEXT_LINK_TO_BLOCK_LINK_CARD = Symbol('LINK_CARD_TEXT_LINK_TO_BLOCK_LINK_CARD');

export const LINK_CARD_BLOCK_LINK_CARD_TO_TEXT_LINK = Symbol('LINK_CARD_BLOCK_LINK_CARD_TO_TEXT_LINK');

export const LINK_CARD_COPY_LINK = Symbol('LINK_CARD_COPY_LINK');

export const LINK_CARD_CUT_LINK = Symbol('LINK_CARD_CUT_LINK');

export const LINK_CARD_DELETE_LINK = Symbol('LINK_CARD_DELETE_LINK');

export const LINK_CARD_UPDATE_DATA = Symbol('LINK_CARD_UPDATE_DATA');

export const LINK_CARD_UNLINK = Symbol('LINK_CARD_UNLINK');

export const LINK_CARD_INSERT_CARD = Symbol('LINK_CARD_INSERT_CARD');


/* #endregion */

/* #region  actions */
export function convertPureLinkToBlockLinkCard(
  link: Text,
  offset: number,
  text: string,
  href: string,
  srcHref: string,
  cardInfo: ILinkCardInfo,
  needFetchLinkInfo?: boolean,
): Action<ConvertPureLinkToBlockLinkCardPayload> {
  return {
    type: LINK_CARD_PURE_LINK_TO_BLOCK_LINK_CARD,
    payload: {
      node: link,
      offset,
      text,
      href,
      srcHref,
      cardInfo,
      needFetchLinkInfo,
    },
  };
}

export function convertTextLinkToBlockLinkCard(
  node: LinkNode,
  text: string,
  href: string,
  cardInfo?: ILinkCardInfo
): Action<ConvertTextLinkToBlockLinkCardPayload> {
  return {
    type: LINK_CARD_TEXT_LINK_TO_BLOCK_LINK_CARD,
    payload: {
      node,
      text,
      href,
      cardInfo
    },
  };
}

export function convertBlockLinkCardToTextLink(
  node: BlockLinkCard,
  data?: {
    text: string;
    href: string;
  }
): Action<ConvertBlockLinkCardToTextLinkPayload> {
  return {
    type: LINK_CARD_BLOCK_LINK_CARD_TO_TEXT_LINK,
    payload: {
      node,
      data
    },
  };
}

export function copyBlockLinkCard(node: BlockLinkCard): Action<CopyOrCutBlockLinkCardPayload> {
  return {
    type: LINK_CARD_COPY_LINK,
    payload: {
      node,
    },
  };
}

export function cutBlockLinkCard(node: BlockLinkCard): Action<CopyOrCutBlockLinkCardPayload> {
  return {
    type: LINK_CARD_CUT_LINK,
    payload: {
      node,
    },
  };
}

export function deleteBlockLinkCard(node: BlockLinkCard): Action<CopyOrCutBlockLinkCardPayload> {
  return {
    type: LINK_CARD_DELETE_LINK,
    payload: {
      node,
    },
  };
}

export function updateBlockLinkCardData(node: BlockLinkCard, text: string, href: string, cardInfo?: ILinkCardInfo): Action<UpdateBlockLinkCardDataPayload> {
  return {
    type: LINK_CARD_UPDATE_DATA,
    payload: {
      node,
      text,
      href,
      cardInfo
    },
  };
}

export function unlinkBlockCardData(node: BlockLinkCard): Action<UnlinkBlockLinkCardPayload> {
  return {
    type: LINK_CARD_UNLINK,
    payload: {
      node,
    },
  };
}

export function insertBlockLinkCard(
  text: string,
  href: string,
  cardInfo?: ILinkCardInfo,
): Action<InsertBlockLinkCardPayload> {
  return {
    type: LINK_CARD_INSERT_CARD,
    payload: {
      text,
      href,
      cardInfo,
    },
  };
}
/* #endregion */